import {
    ChangeDetectorRef,
    Component,
    OnInit,
    AfterViewInit,
    Input,
    Output,
    EventEmitter,
    ViewEncapsulation,
    ViewChild,
    HostListener
} from '@angular/core';

@Component({
    selector: 'ng-image-slider',
    template: `
    <div class="ng-image-slider">
        <div class="container">
            <div class="main" #sliderMain>
                <div
                    class="main-inner"
                    [ngStyle]="{'margin-left':leftPos+'px', 'width':imageParentDivWidth+'px', 'transition': effectStyle}">
                    <div
                        [ngClass]="{'image-popup': imagePopup}"
                        class="img-div"
                        *ngFor="let img of imageObj; let i = index" (click)="imageOnClick(i)" 
                        #imageDiv>
                        <img class="img-fluid" [src]="img.thumbImage" />
                    </div>
                </div>
                <a [ngClass]="{'disable': sliderPrevDisable}" class="prev icons prev-icon" (click)="prev()">&lsaquo;</a>
                <a [ngClass]="{'disable': sliderNextDisable}" class="next icons next-icon" (click)="next()">&rsaquo;</a>
            </div>
        </div>
    </div>
    <div class="img-lightbox" *ngIf="ligthboxShow">
        <div class="lightbox-wrapper">
            <div class="lightbox-div" >
                <div class="pre-loader">
                    <div class="mnml-spinner"></div>
                </div>
                <a class="close" (click)="close()">&times;</a>
                <img *ngIf="currentImageSrc" [ngClass]="{'show': showImage, 'hide': !showImage}" [src]="currentImageSrc">
                <a [ngClass]="{'disable': lightboxPrevDisable}" class="prev icons prev-icon" (click)="prevImage()">&lsaquo;</a>
                <a [ngClass]="{'disable': lightboxNextDisable}" class="next icons next-icon" (click)="nextImage()">&rsaquo;</a>
            </div>
        </div>
    </div>
    `,
    styles: [`.ng-image-slider{display:inline-block;width:100%}.ng-image-slider .container .main{overflow-x:hidden;position:relative;height:200px;overflow-y:hidden}.ng-image-slider .container .main .main-inner{width:1760px;padding:0}.ng-image-slider .container .main .main-inner .img-div{width:205px;height:200px;display:inline-block;float:left;margin-right:3px;margin-left:3px;position:relative;border-radius:10px}.ng-image-slider .container .main .main-inner .img-div.image-popup{cursor:pointer}.ng-image-slider .container .main .main-inner .img-div img{max-width:100%;max-height:100%;position:absolute;top:0;bottom:0;margin:auto;height:100%;width:100%;left:0;right:0}.ng-image-slider .container .main .next,.ng-image-slider .container .main .prev{position:absolute;right:10px;top:50%;background-color:#fff;border-radius:50%;cursor:pointer;margin-top:-16px;outline:0;width:35px;height:35px;font-size:35px;line-height:30px;z-index:1;transition:all .5s ease-in-out}.ng-image-slider .container .main .next:hover,.ng-image-slider .container .main .prev:hover{background-color:#d4cdcd;background-position:-192px -415px}.ng-image-slider .container .main .next.disable,.ng-image-slider .container .main .prev.disable{color:#bbb;background-color:#fff}.ng-image-slider .container .main .prev{left:10px}.ng-image-slider .container .main .prev:hover{background-position:-194px -450px}@media (max-width:1199px){.img-slider .container .main .main-inner .img-div{width:170px;max-width:100%}}@media (max-width:991px){.img-slider .container .main .main-inner .img-div{width:297px;max-width:100%}}@media (max-width:768px){.img-slider .container .main .main-inner .img-div{width:247px;max-width:100%}}@media (max-width:576px){.img-slider .container .main .main-inner .img-div{width:350px;max-width:100%}.img-slider .container .main .main-inner .img-div img{width:280px}}.img-lightbox{position:fixed;z-index:1031;background-color:rgba(0,0,0,.6);width:100%;height:100%;top:0;overflow:hidden;transition:all .5s ease-in-out;left:0;text-align:center}.img-lightbox .lightbox-wrapper{position:fixed;top:0;left:0;width:calc(100% - 100px);height:calc(100% - 100px);margin:50px;background-image:url(data:image/gif;base64,);background-repeat:no-repeat;background-position:center center;background-color:#1f1f1f;transition:all .5s ease;display:flex;justify-content:center;align-items:center;border-radius:10px;padding:10px}.img-lightbox .lightbox-wrapper .lightbox-div{width:100%;height:100%}.img-lightbox .lightbox-wrapper .lightbox-div .pre-loader{background-color:inherit}.img-lightbox .lightbox-wrapper .lightbox-div img{transition:all .5s ease-in-out;opacity:0;max-width:98%;max-height:98%;position:absolute;z-index:11;top:0;bottom:0;margin:auto;left:0;right:0}.img-lightbox .lightbox-wrapper .lightbox-div img.show{opacity:1}.img-lightbox .lightbox-wrapper .lightbox-div img.hide{opacity:0}.img-lightbox .lightbox-wrapper .lightbox-div .close{position:absolute;top:-10px;right:-10px;color:#ea0a3f;background-color:#fff;padding:0;opacity:1;border-radius:50%;line-height:25px;height:25px;width:25px;z-index:12;font-size:25px;font-weight:700;cursor:pointer}.img-lightbox .lightbox-wrapper .lightbox-div .next,.img-lightbox .lightbox-wrapper .lightbox-div .prev{position:absolute;right:10px;top:50%;background-color:#fff;border-radius:50%;cursor:pointer;margin-top:-16px;width:38px;height:38px;font-size:35px;z-index:12;line-height:30px;outline:0;color:#000;transition:all .3s ease-in-out}.img-lightbox .lightbox-wrapper .lightbox-div .next:hover,.img-lightbox .lightbox-wrapper .lightbox-div .prev:hover{background-color:#d4cdcd}.img-lightbox .lightbox-wrapper .lightbox-div .next.disable,.img-lightbox .lightbox-wrapper .lightbox-div .prev.disable{color:#bbb;background-color:#fff}.img-lightbox .lightbox-wrapper .lightbox-div .prev{left:10px}@media (max-width:768px){.img-lightbox .lightbox-wrapper{width:calc(100% - 40px);height:calc(100% - 40px);margin:20px}}`]
})
export class NgImageSliderComponent implements OnInit {
    // for slider
    sliderMainDivWidth: number = 0;
    imageParentDivWidth: number = 0;
    totalImageCount: number = 0;
    imageObj: Array<object> = [];
    leftPos: number = 0;
    effectStyle: string = 'all 1s ease-in-out';
    speed: number = 1; // default speed in second
    sliderPrevDisable: boolean = false;
    sliderNextDisable: boolean = false;
    @ViewChild('sliderMain') sliderMain;
    @Input() imageSize: number = 211;
    @Input() imageShowCount: number = 3;
    @Input() infinite: boolean = false;
    @Input() imagePopup: boolean = true;
    @Input()
    set animationSpeed(data: number) {
        if (data
            && typeof (data) === 'number'
            && data >= 1
            && data <= 5) {
            this.speed = Math.floor(data);
        }
    }
    @Input() set images(imgObj) {
        if (imgObj && imgObj.length) {
            this.imageObj = imgObj;
            this.totalImageCount = imgObj.length - this.imageShowCount; // total image - total showing image
            this.imageParentDivWidth = imgObj.length * this.imageSize;
        }
    }
    @Output() imageClick = new EventEmitter<number>();

    // for lightbox
    currentImageSrc: string;
    ligthboxShow: boolean = false;
    activeImageIndex: number = 0;
    lightboxNextDisable: boolean = false;
    lightboxPrevDisable: boolean = false;
    showImage: boolean = true;
    @ViewChild('imageDiv') imageDiv;

    @HostListener('window:resize', ['$event'])
    onResize(event) {
        if (this.imageDiv.nativeElement.offsetWidth) {
            this.imageSize = +this.imageDiv.nativeElement.offsetWidth + 6; // addeing padding with image width
            this.imageParentDivWidth = this.imageObj.length * this.imageSize;
            this.leftPos = this.infinite ? -1 * this.imageSize : 0;
        }
        if (this.sliderMain.nativeElement.offsetWidth) {
            this.sliderMainDivWidth = this.sliderMain.nativeElement.offsetWidth;
        }
    }
    @HostListener('document:keyup', ['$event'])
    handleKeyboardEvent(event: KeyboardEvent) {
        if (event.key.toLowerCase() === 'arrowright') {
            this.nextImage();
        }

        if (event.key.toLowerCase() === 'arrowleft') {
            this.prevImage();
        }

        if (event.key.toLowerCase() === 'escape') {
            this.close();
        }
    }

    constructor(private cdRef: ChangeDetectorRef) { }

    ngOnInit() {
        // for slider
        if (this.infinite) {
            this.effectStyle = 'none';
            this.leftPos = -1 * this.imageSize;
            this.imageObj.unshift(this.imageObj[this.imageObj.length - 1]);
        }
    }

    // for slider
    ngAfterViewInit() {
        if (this.imageDiv.nativeElement.offsetWidth) {
            this.imageSize = +this.imageDiv.nativeElement.offsetWidth + 6; // addeing padding with image width
            this.leftPos = this.infinite ? -1 * this.imageSize : 0;
            this.imageParentDivWidth = this.imageObj.length * this.imageSize;
        }
        if (this.sliderMain.nativeElement.offsetWidth) {
            this.sliderMainDivWidth = this.sliderMain.nativeElement.offsetWidth;
        }
        this.nextPrevSliderButtonDisable();
        this.cdRef.detectChanges();
    }

    imageOnClick(index) {
        if (this.imagePopup) {
            this.activeImageIndex = index;
            this.showLightbox();
        }
        this.imageClick.emit(index);
    }

    prev() {
        if (this.infinite) {
            this.infinitePrevImg();
        } else {
            this.prevImg();
        }

        this.nextPrevSliderButtonDisable();
    }

    next() {
        if (this.infinite) {
            this.infiniteNextImg();
        } else {
            this.nextImg();
        }

        this.nextPrevSliderButtonDisable();
    }

    prevImg() {
        if (0 > this.leftPos) {
            this.leftPos += this.imageSize;
        }
    }

    nextImg() {
        if (this.imageParentDivWidth + this.leftPos > this.sliderMainDivWidth) {
            this.leftPos -= this.imageSize;
        }
    }

    infinitePrevImg() {
        this.effectStyle = `all ${this.speed}s ease-in-out`;
        this.leftPos = 0;

        setTimeout(() => {
            this.effectStyle = 'none';
            this.leftPos = -1 * this.imageSize;
            this.imageObj.unshift(this.imageObj[this.imageObj.length - 2]);
            this.imageObj.pop();
        }, this.speed * 1000);
    }

    infiniteNextImg() {
        const firstImageIndex = 1;
        this.effectStyle = `all ${this.speed}s ease-in-out`;
        this.leftPos = -2 * this.imageSize;

        setTimeout(() => {
            this.effectStyle = 'none';
            this.imageObj.push(this.imageObj[firstImageIndex]);
            this.imageObj.shift();
            this.leftPos = -1 * this.imageSize;
        }, this.speed * 1000);
    }

    nextPrevSliderButtonDisable() {
        this.sliderNextDisable = false;
        this.sliderPrevDisable = false;
        if (!this.infinite) {
            if (this.imageParentDivWidth + this.leftPos <= this.sliderMainDivWidth) {
                this.sliderNextDisable = true;
            }

            if (this.leftPos >= 0) {
                this.sliderPrevDisable = true;
            }
        }
    }

    // for lightbox
    showLightbox() {
        if (this.imageObj.length && this.imageObj[0]['image']) {
            let imageSrc = this.imageObj[0]['image'];
            if (this.imageObj[this.activeImageIndex]) {
                imageSrc = this.imageObj[this.activeImageIndex]['image'];
            }
            this.getImage(imageSrc);
            this.nextPrevLigthboxButtonDisable();
            this.ligthboxShow = true;
        }
    }

    nextImage() {
        if (this.infinite && this.activeImageIndex + 1 >= this.imageObj.length) {
            this.activeImageIndex = 0;
        }

        if (this.activeImageIndex + 1 < this.imageObj.length
            && this.imageObj[this.activeImageIndex + 1]
            && this.imageObj[this.activeImageIndex + 1]['image']) {
            this.activeImageIndex++;
            const imageSrc = this.imageObj[this.activeImageIndex]['image'];
            this.getImage(imageSrc);
            this.nextPrevLigthboxButtonDisable();
        }
    }

    prevImage() {
        if (this.infinite && this.activeImageIndex - 1 <= 0) {
            this.activeImageIndex = this.imageObj.length;
        }

        if (this.activeImageIndex - 1 > -1
            && this.imageObj[this.activeImageIndex - 1]
            && this.imageObj[this.activeImageIndex - 1]['image']) {
            this.activeImageIndex--;
            const imageSrc = this.imageObj[this.activeImageIndex]['image'];
            this.getImage(imageSrc);
            this.nextPrevLigthboxButtonDisable();
        }
    }

    nextPrevLigthboxButtonDisable() {
        this.lightboxNextDisable = false;
        this.lightboxPrevDisable = false;
        if (!this.infinite) {
            if (this.activeImageIndex >= this.imageObj.length - 1) {
                this.lightboxNextDisable = true;
            }

            if (this.activeImageIndex <= 0) {
                this.lightboxPrevDisable = true;
            }
        }
    }

    close() {
        this.ligthboxShow = false;
    }

    getImage(url) {
        const _this = this;
        // this.currentImageSrc = '';
        this.showImage = false;
        if (url) {
            const image = new Image();
            image.onload = function () {
                setTimeout(() => {
                    _this.currentImageSrc = url;
                    _this.showImage = true;
                }, 0);
            };
            image.src = url;
        }
    }
}
